@import "admin/variables";

.otf-customize-highlight,
.otf-customize-highlight-body {
    pointer-events: none;
    z-index: 500;
    border: 1px dashed $main-color;
    .customize-partial-edit-shortcut {

        button {
            left: 0;
        }
    }
    .button-customize-group {
        left: 0;
        right: 0;
        margin: -20px auto 0;
        text-align: center;
        .otf-button-customizer,
        .customize-partial-edit-shortcut {
            position: static;
            float: none;
            &.hide {
                display: none;
            }
        }
    }
}


.otf-highlight-body {
    .otf-customize-highlight-body {
        display: block!important;
        position: fixed;
        border: 0;
        top: 25px;
        left: 3px;
        width: 100%;
        .button-customize-group {
            text-align: left;
        }
        .otf-button-customizer{
            display: none;
        }
    }
}


